/* global */

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Segoe UI', sans-serif;
  background-color: #f7f7f7;
}

ul {
  list-style-type: none;
}

a {
  text-decoration: none;
  color: #42a5f5;
}

.button {
  display: inline-block;
  position: relative;
  padding: 12px 36px;
  margin: 10px 0;
  color: #fff;
  font-size: 16px;
  font-weight: 600;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 2px;
  background-color: #2c2c2c00;
  border: none;
  cursor: pointer;
  overflow: hidden;
  transition: all 0.3s ease-in-out;
  animation-duration: 0.3s;
  animation-timing-function: ease-out;
}

.button:before {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #fff;
  transform: scaleY(0);
  transform-origin: bottom;
  transition: transform 0.2s ease-in-out;
}

.button:hover:before {
  transform: scaleY(1);
}

.button:hover {
  color: #ffffff;
}

.active {
  background-color: #0078c200 !important;
  animation-name: button-click;
}

/* Default styles */
button {
  padding: 12px 36px;
  font-size: 16px;
}

/* Styles for screens smaller than 768px */
@media (max-width: 795px) {
  .button {
    padding: 8px 24px;
    font-size: 14px;
  }
  
  .App-header h1 {
    font-size: 1.5rem;
  }
  
  nav button {
    font-size: 1rem;
  }
  
  .App-section h2 {
    font-size: 1.25rem;
  }
  
  .App-section ul li a {
    padding: 0.25rem 0.5rem;
    font-size: 0.9rem;
  }

  .App-main {
      max-width: 100%;
  }
}

/* Styles for screens smaller than 480px */
@media (max-width: 479px) {
  .button {
    padding: 6px 18px;
    font-size: 12px;
  }
  
  .App-header h1 {
    font-size: 1.2rem;
  }
  
  nav button {
    font-size: 0.8rem;
  }
  
  .App-section h2 {
    font-size: 1rem;
  }
  
  .App-section ul li a {
    padding: 0.2rem 0.4rem;
    font-size: 0.8rem;
  }
}


@keyframes button-click {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(0.9);
  }
  100% {
    transform: scale(1);
  }
}

/* app  */

.App {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  background-color: #f7f7f7;
}

.App-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #2c2c2c;
  color: #fff;
  padding: 1rem;
}

.App-header h1 {
  font-size: 2rem;
}

nav ul {
  display: flex;
}

nav li {
  margin-left: 1rem;
}

nav li:first-child {
  margin-left: 0;
}

nav button {
  font-size: 1.2rem;
}

.App-main {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 2rem;
  background-color: #f7f7f7;
}

.App-section {
  margin-bottom: 2rem;
}

.App-section h2 {
  font-size: 1.5rem;
  margin-bottom: 0.5rem;
}

.App-section ul li a {
  color: #111;
  background-color: #fff;
  padding: 0.5rem 1rem;
  border-radius: 0.25rem;
  transition: all 0.3s ease;
}

.App-section ul li a:hover {
  background-color: #0077c2;
  color: #fff;
  transform: translateY(-3px);
  box-shadow: 0 5px 15px rgba(0,0,0,.3);
}

.App-video {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 40vh;
  background-color: #f7f7f7;
}

footer {
  background-color: #2c2c2c;
  color: #fff;
  text-align: center;
  padding: 1rem;
  margin-top: auto;
}