.RandomVideo {
  position: absolute;
  bottom: 200px;
  left: 50%;
  transform: translateX(-50%) scale(0.5);
  width: 30%;
  background-color: #ffffff;
  box-shadow: 0px 5px 20px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  padding: 20px;
  box-sizing: border-box;
  opacity: 0;
  transition: opacity 0.3s, transform 0.3s;
}

.RandomVideo.loaded {
  opacity: 1;
  transform: translateX(-50%) scale(1);
}

.RandomVideo iframe {
  width: 100%;
  height: 250px;
  border: none;
}

.RandomVideo p {
  font-size: 16px;
  margin-top: 0;
}

.random-video-container {
  position: relative;
  width: 100%;
  height: 100%;
}

.RandomVideo-header {
  margin-left: 180px;
  margin-bottom: 10px;
}

.RandomVideo-video-container {
  position: relative;
  width: 100%;
  height: 250px;
  overflow: hidden;
  border-radius: 10px;
}

.RandomVideo-loader {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3);
  display: flex;
  justify-content: center;
  align-items: center;
}

@media screen and (max-width: 795px) {
  .RandomVideo {
    bottom: 100px;
    transform: translateX(-50%) scale(0.4);
    width: 75%;
    padding: 10px;
  }

  .RandomVideo iframe {
    height: 200px;
  }

  .RandomVideo-header {
    margin-left: 80px;
  }
}
